import {
  Component,
  ViewChild,
  ElementRef,
  OnInit,
  OnDestroy,
  AfterViewInit,
} from '@angular/core'
import { Router, RouterModule } from '@angular/router'
import { LoadingController, IonicModule } from '@ionic/angular'
import {
  confirmUserAttribute,
  sendUserAttributeVerificationCode,
} from 'aws-amplify/auth'
import { TranslateModule, TranslateService } from '@ngx-translate/core'
import { AlertController } from '@ionic/angular/standalone'
import { CyclistsService } from 'src/api-cyclist'
import {
  AbonnementWrapperService,
  CreateAbonnementParams,
} from 'src/app/services/api-wrappers/abonnement.service'
import { AccountService } from 'src/app/services/api-wrappers/account.service'
import { HeaderComponent } from 'src/app/components/header/header.component'
import { FormsModule } from '@angular/forms'
import { CommonModule } from '@angular/common'
import { AuthService } from 'src/app/services/external/auth.service'
import { SubscriptionProcessService } from 'src/app/services/utils/subscription-process.service'
import { WalletService } from 'src/app/services/api-wrappers/wallet.service'
import { firstValueFrom } from 'rxjs'
import { ErrorHandlerService } from 'src/app/services/utils/error-handler.service'

@Component({
  selector: 'app-verify-phone',
  templateUrl: './verify-phone.page.html',
  styleUrls: ['./verify-phone.page.scss'],
  imports: [
    IonicModule,
    CommonModule,
    FormsModule,
    TranslateModule,
    HeaderComponent,
    RouterModule,
  ],
})
export class VerifyPhonePage implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild('otpInput') otpInput!: ElementRef<HTMLInputElement>
  allowVerificationCodeResend: boolean = false
  verificationResendTimer: number = 20
  private countdownInterval: any
  verificationCode: string = ''
  selectedForfait: any
  slots: string[] = new Array(6).fill('')
  activeSlotIndex: number = 0
  userPhoneNumber: string = ''

  constructor(
    private loadingController: LoadingController,
    private translateService: TranslateService,
    private loadingCtrl: LoadingController,
    private translate: TranslateService,
    private cyclistService: CyclistsService,
    private AbonnementWrapperService: AbonnementWrapperService,
    private router: Router,
    private accountService: AccountService,
    private alertController: AlertController,
    private authService: AuthService,
    private subscriptionProcess: SubscriptionProcessService,
    private walletService: WalletService,
    private errorHandler: ErrorHandlerService
  ) {}

  async ngOnInit() {
    await this.subscriptionProcess.updateStep('phone')
    await this.sendVerificationCode()
    this.userPhoneNumber = await this.authService.getUserPhoneNumber()
  }

  startResendTimer() {
    this.allowVerificationCodeResend = false
    this.verificationResendTimer = 20

    if (this.countdownInterval) clearInterval(this.countdownInterval)

    this.countdownInterval = setInterval(() => {
      if (this.verificationResendTimer > 0) {
        this.verificationResendTimer--
      } else {
        this.allowVerificationCodeResend = true
        clearInterval(this.countdownInterval)
      }
    }, 1000)
  }

  async sendVerificationCode() {
    this.startResendTimer()
    try {
      await sendUserAttributeVerificationCode({
        userAttributeKey: 'phone_number',
      })
    } catch (error: any) {
      this.errorHandler.handleError(error)
    }
  }

  async deleteAccount() {
    const alert = await this.alertController.create({
      header: this.translateService.instant('verify_phone.delete_account'),
      message: this.translateService.instant(
        'verify_phone.delete_account_message'
      ),
      buttons: [
        { text: 'Annuler', role: 'cancel' },
        {
          text: 'Supprimer',
          cssClass: 'text-danger',
          handler: async () => {
            try {
              await this.accountService.deleteAccount()
              await this.authService.signOut()
            } catch (err) {
              this.errorHandler.handleError(err)
            }
          },
        },
      ],
    })
    await alert.present()
  }

  onInput(event: Event) {
    const input = event.target as HTMLInputElement
    const value = input.value.replace(/[^0-9]/g, '')

    if (value.length === 6) {
      this.slots = value.split('')
      this.verificationCode = value
      this.activeSlotIndex = 5
      this.verifyCode()
      return
    }

    const lastChar = value[value.length - 1]
    if (lastChar) {
      this.slots[this.activeSlotIndex] = lastChar
      if (this.activeSlotIndex < 5) {
        this.activeSlotIndex++
      }
    }

    this.verificationCode = this.slots.join('')
    input.value = this.verificationCode
  }

  onKeyDown(event: KeyboardEvent) {
    switch (event.key) {
      case 'ArrowLeft':
        event.preventDefault()
        this.activeSlotIndex = Math.max(0, this.activeSlotIndex - 1)
        break
      case 'ArrowRight':
        event.preventDefault()
        this.activeSlotIndex = Math.min(5, this.activeSlotIndex + 1)
        break
      case 'Backspace':
        event.preventDefault()
        if (this.slots[this.activeSlotIndex]) {
          this.slots[this.activeSlotIndex] = ''
          this.verificationCode = this.slots.join('')
        } else if (this.activeSlotIndex > 0) {
          this.activeSlotIndex--
          this.slots[this.activeSlotIndex] = ''
          this.verificationCode = this.slots.join('')
        }
        break
      default:
        if (/^\d$/.test(event.key)) {
          event.preventDefault()
          this.slots[this.activeSlotIndex] = event.key
          this.verificationCode = this.slots.join('')
          if (this.activeSlotIndex < 5) {
            this.activeSlotIndex++
          }
          if (this.verificationCode.length === 6) {
            this.verifyCode()
          }
        }
    }
    this.otpInput.nativeElement.value = this.verificationCode
    this.setInputCursor(this.activeSlotIndex)
  }

  onSlotClick(index: number) {
    this.activeSlotIndex = index
    this.otpInput.nativeElement.focus()
    this.setInputCursor(index)
  }

  setInputCursor(position: number) {
    requestAnimationFrame(() => {
      this.otpInput.nativeElement.setSelectionRange(position, position)
    })
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.otpInput.nativeElement.focus()
    }, 0)
  }

  async verifyCode() {
    if (this.verificationCode.length !== 6) return

    const loading = await this.loadingController.create({
      message: this.translateService.instant('verify_phone.verifying'),
    })

    try {
      await loading.present()
      await confirmUserAttribute({
        userAttributeKey: 'phone_number',
        confirmationCode: this.verificationCode,
      })

      await new Promise<void>((resolve, reject) => {
        this.cyclistService.confirmCyclist().subscribe({
          next: () => {
            const cyclistState = this.accountService.cyclistState()
            if ('cyclist' in cyclistState) {
              const updatedCyclist = {
                ...cyclistState.cyclist,
                status: 'active',
              }
              // Use checkCyclistAccount to properly determine state based on role
              this.accountService.checkCyclistAccount(updatedCyclist)

              if (
                this.accountService.hasBlockedReason('Account not confirmed')
              ) {
                this.accountService.removeBlockedReason('Account not confirmed')
              }
            }
            resolve()
          },
          error: reject,
        })
      })

      loading.message = this.translateService.instant(
        'verify_phone.confirming_cyclist'
      )

      // Handle external users differently
      const cyclistState = this.accountService.cyclistState()

      if (
        'cyclist' in cyclistState &&
        (cyclistState.cyclist.role == 'guest' ||
          cyclistState.cyclist.registrations?.data?.[0]?.role == 'guest')
      ) {
        // Validate terms for external users
        await loading.dismiss()
        await this.router.navigate(['/account/settings/cgu'], {
          queryParams: { nextUrl: '/renting' },
          replaceUrl: true,
        })
        return
      }

      await loading.dismiss()
      // Regular cyclist flow
      if (await this.subscriptionProcess.hasForfaitSelected()) {
        const payload =
          await this.subscriptionProcess.getCreateAbonnementPayload()
        if (payload) {
          await this.createAbonnement(payload)
          await this.subscriptionProcess.clearProcess()
        }
        await this.router.navigate(['/abo-congrats'])
      } else {
        // Vérifier s'il y a un code de réduction à appliquer au portefeuille
        const process = await firstValueFrom(
          this.subscriptionProcess.getCurrentProcess()
        )
        if (process?.reduction) {
          await this.walletService.applyCodePromo(process.reduction)
        }
        await this.subscriptionProcess.clearProcess()
        await this.router.navigate(['/abo-congrats'])
      }
    } catch (error) {
      await loading.dismiss()
      this.errorHandler.handleError(error)
    }
  }

  async createAbonnement(payload: CreateAbonnementParams) {
    const loading = await this.loadingCtrl.create({
      message: this.translate.instant('subscription.creation.loading'),
    })
    await loading.present()

    await this.AbonnementWrapperService.createAbonnement(payload).finally(
      async () => {
        await loading.dismiss()
      }
    )
  }

  ngOnDestroy() {
    if (this.countdownInterval) {
      clearInterval(this.countdownInterval)
    }
  }
}
